import request from '@/utils/request'

/**
 * 列表
 * @param query 查询参数，包含pageNum、pageSize以及其他参数
 */
export function fetchList (query) {
  return request({
    url: '/sys/userLogs',
    method: 'get',
    params: query
  })
}

/**
 * 详情
 * @param id 主键
 */
export function fetchUserLog (id) {
  return request({
    url: '/sys/userLogs/' + id,
    method: 'get'
  })
}

/**
 * 添加
 * @param data 对象
 */
export function createUserLog (data) {
  return request({
    url: '/sys/userLogs',
    method: 'post',
    data
  })
}

/**
 * 修改
 * @param data 对象
 */
export function updateUserLog (data) {
  return request({
    url: '/userLogs/' + data.id,
    method: 'put',
    data
  })
}

/**
 * 删除
 * @param id 主键
 */
export function deleteUserLog (id) {
  return request({
    url: '/sys/userLogs/' + id,
    method: 'delete'
  })
}



