<template>
  <div>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0;">
      <el-form :inline="true" :model="listQuery" label-position="left">
        <el-form-item :label="$t('userLog.username')">
          <el-input autofocus v-model="listQuery.username" :placeholder="$t('userLog.username')" clearable @clear="listQuery.username = undefined"></el-input>
        </el-form-item>
        <el-form-item :label="$t('userLog.operation')">
          <el-input v-model="listQuery.operation" :placeholder="$t('userLog.operation')" clearable @clear="listQuery.operation = undefined"></el-input>
        </el-form-item>
        <el-form-item :label="$t('userLog.ip')">
          <el-input v-model="listQuery.ip" :placeholder="$t('userLog.ip')" clearable @clear="listQuery.ip = undefined"></el-input>
        </el-form-item>
        <el-form-item :label="$t('userLog.type')">
          <el-select clearable @clear="listQuery.type = undefined" v-model="listQuery.type" placeholder="请选择">
            <el-option
              v-for="item in ['add', 'update', 'del', 'other']"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="$t('userLog.createTime')">
          <el-date-picker v-model="createTime" type="datetimerange" unlink-panels range-separator="-" start-placeholder="起" end-placeholder="止" value-format="yyyy-MM-dd HH:mm:ss"/>
        </el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search" @click="handleFilter">{{$t('common.search')}}</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <el-table ref="dataTable" :data="list" v-loading="listLoading" :element-loading-text="$t('common.loadingText')" border :fit="true" highlight-current-row stripe @selection-change="handleSelectionChange" @row-click="handleRowClick" tooltip-effect="light">
      <el-table-column type="index" width="50" align="center" />
      <el-table-column :label="$t('userLog.username')" prop="username" align="center" width="120"/>
      <el-table-column :label="$t('userLog.type')" prop="type" align="center" width="80"/>
      <el-table-column :label="$t('userLog.operation')" prop="operation" align="center" width="200"/>
      <el-table-column :label="$t('userLog.method')" prop="method" align="center" />
      <el-table-column show-overflow-tooltip :label="$t('userLog.params')" prop="params" align="center" />
      <el-table-column :label="$t('userLog.ip')" prop="ip" align="center" width="130"/>
      <el-table-column :label="$t('userLog.createTime')" prop="createTime" align="center" width="160"/>
    </el-table>
    <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="listQuery.pageNum" :page-sizes="[20,30,50,100]" :page-size="listQuery.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
    </el-pagination>
  </div>
</template>
<script>
import { fetchList } from '@/api/userLog'

export default {
  name: 'userLog',
  data () {
    return {
      tableKey: 0, // 用于table结构改变时使用，此时无用
      list: null,
      total: null,
      listLoading: true,
      listQuery: {
        pageNum: 1,
        pageSize: 20,
        username: undefined,
        type: undefined,
        operation: undefined,
        ip: undefined,
        beginCreateTime: undefined,
        endCreateTime: undefined
      },
      multipleSelection: [],
      createTime: []
    }
  },
  created () {
    this.getList()
  },
  methods: {
    handleSizeChange (val) {
      this.listQuery.pageSize = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.listQuery.pageNum = val
      this.getList()
    },
    handleRowClick (row) {
      this.$refs.dataTable.toggleRowSelection(row)
    },
    handleSelectionChange (selection) {
      this.multipleSelection = selection.reduce((ary, cur, idx) => {
        ary.push(cur)
        return ary
      }, [])
    },
    handleFilter () {
      this.listQuery.pageNum = 1
      this.getList()
    },
    getList () {
      this.listLoading = true

      if (this.createTime && this.createTime.length === 2) {
        this.listQuery.beginCreateTime = this.createTime[0]
        this.listQuery.endCreateTime = this.createTime[1]
      } else {
        this.listQuery.beginCreateTime = undefined
        this.listQuery.endCreateTime = undefined
      }

      fetchList(this.listQuery).then(response => {
        this.list = response.data.list
        this.total = response.data.total
        this.listLoading = false
      })
    }
  }
}

</script>



